.headerWrap { background:#b32025; color:#fff; 
  font-size: 17px;
    .row{ display:flex; justify-content: space-between;
        align-items: center;
        height:100px;
    img { width:85px; height:65px; }
  }
  .nav { 
    .depth1 { display:flex; 
        li { padding:12px 15px; position: relative;
      } 
      .depth2 { display:none;
        position: absolute; top:100%; left:-35px;
        width:150px; 
        background:#b32025; color:#fff; z-index:999;
        padding:35px 0px 20px; 
        text-align:center; 
        &.on { display:block }
        li { padding:8px 0;
        }
    }
  }
 }

  .login ul { display:flex;
    li { padding:0 10px;}
}
}