@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap);
* { margin:0; padding:0; box-sizing:border-box; outline:none; 
  font-family: 'Nanum Gothic', sans-serif;
}
body {
  font-size:16px; font-family: 'Roboto Condensed', sans-serif;
}
a { color:inherit; text-decoration:none }
ul, ol {list-style:none }
.row { max-width:1400px; margin:0 auto; padding:0 80px }
.row2 { max-width:1100px; margin:0 auto; }
.cf:after { content:''; display:block; clear:both; }
img { max-width:100%; vertical-align: middle ;}
button { cursor:pointer;}
fieldset { display:none }
legend { display:none; }
h1,h2,h3,h4,h5,h6 { font-weight: 100%;}
table { width:100%; border-collapse: collapse;
 table-layout: fixed;}
 .blind { position: absolute; width:1px; height:1px; text-indent:-999999px;
  overflow:hidden;
}


.headerWrap{background:#b32025;color:#fff;font-size:17px}.headerWrap .row{display:flex;justify-content:space-between;align-items:center;height:100px}.headerWrap .row img{width:85px;height:65px}.headerWrap .nav .depth1{display:flex}.headerWrap .nav .depth1 li{padding:12px 15px;position:relative}.headerWrap .nav .depth1 .depth2{display:none;position:absolute;top:100%;left:-35px;width:150px;background:#b32025;color:#fff;z-index:999;padding:35px 0px 20px;text-align:center}.headerWrap .nav .depth1 .depth2.on{display:block}.headerWrap .nav .depth1 .depth2 li{padding:8px 0}.headerWrap .login ul{display:flex}.headerWrap .login ul li{padding:0 10px}

.footerWrap{background:#333;color:#fff;padding:30px 0;margin-top:50px}.footerWrap .row{display:flex;justify-content:space-between}.footerWrap .row p{font-size:14px;padding:10px 0;color:#ddd}.footerWrap .row .footerInfo{width:40%}.footerWrap .row .footerInfo h2{font-size:20px;padding-bottom:10px;color:#111}.footerWrap .row .footerpolicy{width:25%}.footerWrap .row .footerpolicy li{font-size:15px;padding:7px 0}.footerWrap .row .footerpolicy span{color:#ffbc0d;font-weight:bold}.footerWrap .row .footericon{width:20%}.footerWrap .row .footericon img{width:200px}

.homesecWrap .article2{padding:70px 0}.homesecWrap .article2 h2{font-size:33px;text-align:center;padding:20px 0}.homesecWrap .article2 .foodWrap{display:flex;flex-wrap:wrap}.homesecWrap .article2 .foodWrap div{width:33.33%;padding:20px 15px}.homesecWrap .article2 .foodWrap div img{border-radius:20px 20px 0 0}.homesecWrap .article2 .foodWrap div p{border:1px solid #ddd;padding:20px 20px 40px;font-size:18px;font-weight:bold;border-radius:0 0 20px 20px;box-shadow:0px 2px 0px #eee;height:120px}

