.footerWrap { background:#333; color:#fff;
    padding:30px 0; margin-top:50px;
    .row{
             display:flex; justify-content: space-between;
            p { font-size:14px; padding:10px 0; color:#ddd }
            .footerInfo{ width:40%;
                h2 { font-size:20px; padding-bottom:10px;
                    color:#111
                }
            }
            .footerpolicy{ width:25%;
                li { font-size:15px; padding:7px 0;}
                span { color:#ffbc0d; font-weight:bold}
            }
            .footericon{ width:20%; 
                img { width:200px; }
            }
    }
}